export default {
  created() {
    this.pwFormType = this.pwFormTypeDef["certChangePwForm"];
  },
  beforeCreate() {
    //window.addEventListener('load', this.setArrLength);
  },
  data: function () {
    return {
      pwFormType: {},
      pwFormTypeDef: {
        certEnterPwForm: {
          // 선택한 인증서 암호입력 폼 (1줄)
          id: "certEnterPw",
        },
        newCertPwForm: {
          // (발급/재발급)새로운 인증서 암호입력 (2줄)
          id: "newCertPw",
        },
        certChangePwForm: {
          // 인증서 암호변경 (3줄)
          id: "certChangePw",
        },
      },

      inputPw: [
        // 발급/재발급
        { idx: 1 },
        { idx: 2 },
        { idx: 3 },
      ],
    };
  },
  mounted() {
    this.initPolicy();
  },

  methods: {
    initPolicy() {
      var param = this.$store.state.policy;
      // sdk 호출 - 정책 설정
      //console.log(param);
      if (param.type) {
        this.pwFormType = this.pwFormTypeDef[param.type];
      }
    },
  },
};
